<template>
    <div class="main-quiz-container--section main-quizzes-table--section">
        <HeaderComponent></HeaderComponent>

        <div class="main-not-loaded-msg--area" v-if="!isLoaded">
            <div class="main-fake-content--elem">
                <div class="main-fake-header--elem">
                    <div class="__full main-fake-text--elem"><span class="gradient"></span></div>
                    <div class="__180px main-fake-text--elem"><span class="gradient"></span></div>
                    <div class="__105px main-fake-text--elem"><span class="gradient"></span></div>
                </div>
                <div class="main-fake-links--elem gradient"></div>
                <div class="main-fake-links--elem gradient"></div>
                <div class="main-fake-msg--elem">Loading Content ...</div>
            </div>
        </div>

        <div v-if="isLoaded"><!-- v-if="isLoaded" -->
            <div class="main-header-of-quizzes-list-table--area" v-if="quizzes.length">
                <div class="main-column-of-table--elem __full">Title</div>
                <div class="main-column-of-table--elem __180px">Category</div>
                <div class="main-column-of-table--elem __105px">Nbr. Questions </div>
            </div>
            <div class="main-body-of-quizzes-list-table--area" v-for="(quiz, index) in quizzes" :key="index">
                <div class="main-border-gradient--ui">
                    <router-link :to="`/quiz/${quiz.id}`">
                        <div class="main-quiz-name--elem __full">{{ quiz.name }}</div>
                        <div class="main-quiz-category--elem __180px">{{ quiz.category }}</div>
                        <div class="main-quiz-question-lenght--elem __105px"><span>{{ quiz.questions.length }}</span></div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import HeaderComponent from './../components/HeaderComponent';

export default {
    data() {
        return {
            quizzes: [],

            //loaded data
            isLoaded: false
        }
    },
    created() {
        this.getQuizzes();
    },
    methods: {
        async getQuizzes(){
            try {
                const data = await axios({
                    method: "GET",
                    url: "/api/v1/quiz/"
                });

                this.quizzes = data.data.data.data;
                this.isLoaded = true;
            } catch (error) {
                return this.$router.push('/error');
            }
        },
    },
    components: { HeaderComponent }
}
</script>

